<template>
    <div>
        <div class="box">
            <header>
                <a>
                    <img src="../../images/110.png" alt="" @click="back">
                </a>
                <span>
                    搜素
                </span>
            </header>
            <div class="content">
                <input type="text" placeholder="请输入商家或美食名称" v-model="val">
                <button @click="add">提交</button>
                 <div class="sousuo">
                     <span>搜索历史</span> 
                </div>
                <div class="history">
                    <div class="placehistory" v-for="(item,index) in datas"::key="index">
                        <img :src="'https://fuss10.elemecdn.com'+item.image_path" alt=""/>
                        <span>{{item.name}}</span>
                        <span>{{item.address}}</span>
                        <span>{{item.rating_count}}</span>
                    </div>
                </div>
                <div class="xiaoguo">
                    <span>效果</span>
                    <p>x</p>
                </div>
                <div class="qingkong">
                    清空搜索历史
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import axios from 'axios'
export default {
    data(){
        return{
            val:'',
            datas:[]
        }
    },
    mounted(){
        function  rem(){
		document.documentElement.style.fontSize=document.documentElement.clientWidth/7.5+"px";
	}
	//onresize  当屏幕的尺寸发生改变的时候会触发的事件
	rem();
  window.onresize=rem;

    },
    methods:{
        back(){
            this.$router.go(-1)
        },
        add(){
            this.axios.get('https://elm.cangdu.org/v4/restaurants?extras[]=restaurant_activity&geohash='+this.$route.params.geohash+'&keyword='+this.val+'&type=search')
            .then((res)=>{
                console.log(res.data)
                // this.datas = res.data;
            })
        }
    }
}
</script>

<style lang="less" scoped>
      .box{
          width: 100%;
          header{
              position: fixed;
              top: 0;
              left: 0;
              width: 100%;
              height: 1rem;
              background: #3792E5;
              text-align: center;
              line-height: 0.6rem;
              a{
                 text-decoration: none;
                 img{
                     position: absolute;
                     top: 0.25rem;
                     left: 0.2rem;
                     width: 0.4rem;
                 }
               }
            span{
                text-align: center;
                color: white;
                font-size: 0.4em;
            }
          }
          .content{
              width: 100%;
              margin-top: 0.6rem;
              input{
                  width: 270px;
                  height: 35px;
                  margin-left: 15px;
                  background: #F2F2F2;
                  border: none;
                  text-indent: 10px;
                  font-size: 18px;
                  font-weight: 550;
              }
              button{
                  width: 70px;
                  height: 35px;
                  background: #3792E5;
                  color: white;
                  border: none;
                  border-radius: 5px;
              }
          }
          .sousuo{
                // padding-left: 1rem;
                width: 100%;
                height: 50px;
                background: #F5F5F5;
                color: black;
                margin-top: 0.5rem;
                height: 40px;
                span{
                    display: block;
                    margin-left: 0.3rem;
                    font-size: 17px;
                    font-weight: 550;
                    line-height: 40px;
                }
             }
          .xiaoguo{
              width: 100%;
              position: relative;
              height: 50px;
              border-bottom: 1px solid #E4E4E4;
              span{
                  display: block;
                  position: absolute;
                  left: 20px;
                  top: 10px;
                  font-size: 18px;
              }
              p{
                  display: block;
                  position: absolute;
                  right: 15px;
                  top: -7px;
                  font-weight: 600;
                  font-size: 17px;
              }
          }
          .fan{
              width: 100%;
              position: relative;
              height: 50px;
              border-bottom: 1px solid #E4E4E4;
              span{
                  display: block;
                  position: absolute;
                  left: 20px;
                  top: 10px;
                  font-size: 18px;
              }
              p{
                  display: block;
                  position: absolute;
                  right: 15px;
                  top: -7px;
                  font-weight: 600;
                  font-size: 17px;
              }
          }
          .niurou{
              width: 100%;
              position: relative;
              height: 50px;
              border-bottom: 1px solid #E4E4E4;
              span{
                  display: block;
                  position: absolute;
                  left: 20px;
                  top: 10px;
                  font-size: 18px;
              }
              p{
                  display: block;
                  position: absolute;
                  right: 15px;
                  top: -7px;
                  font-weight: 600;
                  font-size: 17px;
              }
          }
          .qingkong{
              font-size: 20px;
              text-align: center;
              margin-top: 10px;
              color: #3792E5;
              font-weight: 550;
          }
          .history{
              width: 100%;
              .placehistory{
                  width: 100%;
                  span{
                      font-size: 0.3rem;
                  }
              }
          }
      }
</style>



